function passwordChangeIcon() {
  try {
    const items = document.getElementsByClassName('div-password-field');

    for (let i = 0; i < items.length; i++) {
      let switcher = items[i].getElementsByClassName('password-visible-switcher')[0];
      switcher.onclick = function () {
        let item = items[i];
        let input = item.getElementsByTagName('input')[0];
        if (input) {
          const type = input.getAttribute('type') === 'password' ? 'text' : 'password';
          input.setAttribute('type', type);
          if (type === 'password') {
            item.getElementsByClassName('form-password-visible-icon')[0].classList.add('login__form__input__icon__hide');
            item.getElementsByClassName('form-password-hide-icon')[0].classList.remove('login__form__input__icon__hide');
          } else {
            item.getElementsByClassName('form-password-visible-icon')[0].classList.remove('login__form__input__icon__hide');
            item.getElementsByClassName('form-password-hide-icon')[0].classList.add('login__form__input__icon__hide');
          }
        }
      }


    }
  } catch (e) {
    console.log('passwordChangeIcon', e);
  }
}

function resizeWindowListener() {
  try {
    window.addEventListener('resize', function (event) {
      //document.body.clientWidth
      //document.body.clientHeight
      const items = document.getElementsByClassName('mobile-placeholder');

      // eslint-disable-next-line no-empty
      if (document.body.clientWidth && document.body.clientWidth <= 768) {
        for (let i = 0; i < items.length; i++) {
          if (items[i].dataset.moblilePlaceholderText) {
            items[i].placeholder = items[i].dataset.moblilePlaceholderText;
          }
        }

      } else if (document.body.clientWidth && document.body.clientWidth <= 992) {
        for (let i = 0; i < items.length; i++) {
          if (items[i].dataset.tabletPlaceholderText) {
            items[i].placeholder = items[i].dataset.tabletPlaceholderText;
          }
        }
      } else {

        for (let i = 0; i < items.length; i++) {
          if (items[i].dataset.defaultPlaceholderText) {
            items[i].placeholder = items[i].dataset.defaultPlaceholderText;
          }
        }


      }
    });
  } catch (e) {
    console.log('resizeWindowListener', e);
  }
}

function swapperOnClick() {
  try {
    var list = document.querySelectorAll('.swapper-list-item');
    list.forEach(function (item) {
      item.addEventListener('click', function (e) {
        e.preventDefault();
        var select = this.getAttribute('data-swapper-select').split(',');
        var swapClass = this.getAttribute('data-swapper-class').split(',');
        for (let i = 0; i < select.length; i++) {
          var element = document.querySelector('[data-swapper="' + select[i] + '"]');
          if (!element) {
            continue;
          }
          element.classList.toggle(swapClass[i]);
        }
      });
    });
  } catch (e) {
    console.log('swapperOnClick', e);
  }
}

function modalWindows() {
  try {
    var modalButtons = document.querySelectorAll('.js-open-modal'),
      overlay = document.querySelector('.js-overlay-modal'),
      closeButtons = document.querySelectorAll('.js-modal-close');

    if (!modalButtons || !overlay || !closeButtons) {
      return;
    }
    modalButtons.forEach(function (item) {

      item.addEventListener('click', function (e) {

        e.preventDefault();
        var modalId = this.getAttribute('data-modal'),
          modalElem = document.querySelector('.modal[data-modal="' + modalId + '"]');
        modalElem.classList.add('active');
        overlay.classList.add('active');
      });
    });

    closeButtons.forEach(function (item) {

      item.addEventListener('click', function (e) {
        var parentModal = this.closest('.modal');

        parentModal.classList.remove('active');
        overlay.classList.remove('active');
      });

    });

    document.body.addEventListener('keyup', function (e) {
      var key = e.keyCode;

      if (key === 27) {
        document.querySelector('.modal.active').classList.remove('active');
        document.querySelector('.overlay').classList.remove('active');
      }

    }, false);


    overlay.addEventListener('click', function () {
      document.querySelector('.modal.active').classList.remove('active');
      this.classList.remove('active');
    });
  } catch (e) {
    console.log('swapperOnClick', e);
  }
}

